/**
 * Application entry point
 */

// Load application styles
import 'styles/index.scss';

// ================================
// START YOUR APP HERE
// ================================

(function() {

    const SIZE_MULTIPLIER = 0.0085;
    const M__I_FIELDS = [
        "launch",
        "lifespan",
        "support",
        "stats",
        "planet",
        "overview"
    ]
    let cacheImages = [], missionData;

    function init() {

        window.addEventListener('load', function() {
            setSSSize();
        });

        window.addEventListener('resize', function() {
            setSSSize();
        });

        
        let ssEl = document.querySelector('#ss'),
            scEl = document.getElementById('ss-container'),
            pEl = document.getElementById('focus-planet'),
            eEl = document.getElementById('focus-earth'),
            pRect, eRect, focusActive = false, pBs;

        function setSSSize() {
            //1000px 8.5px
            let size = ssEl.clientWidth;
            ssEl.style.fontSize = (size * SIZE_MULTIPLIER) + 'px';
        }

        function overlayPlanet(planetName, fEl, grabBS) {
            let jEl = document.querySelector('#' + planetName + ' .planet'),
                size = jEl.offsetWidth,
                bRect = jEl.getBoundingClientRect();
            fEl.style.boxShadow = window.getComputedStyle(jEl).getPropertyValue('box-shadow');
            setPVals(fEl, bRect);
            fEl.classList.add(planetName);
            if(grabBS) {
                pBs = window.getComputedStyle(jEl).getPropertyValue('box-shadow');
            }
            return bRect;
        }

        function setPVals(el, rect, bs) {
            el.style.width = rect.width + 'px';
            el.style.height = rect.height + 'px';
            el.style.top = rect.top + 'px';
            el.style.left = rect.left + 'px';
            el.style.display = 'block';
            el.style.boxShadow = bs;
        }

        function calcFocusSize(fEl) {
            let sW = ssEl.clientWidth,
                sH = ssEl.clientHeight,
                eW, eLeft, bsW;
            eW = (sW > sH) ? sH * .8 : sW * .8;
            eLeft = (sW - eW) / 2;
            bsW = eW / 4;
            
            fEl.style.width = eW + 'px';
            fEl.style.height = eW + 'px';
            fEl.style.top = '10%';
            fEl.style.left = eLeft + 'px';
            fEl.style.boxShadow = 'inset ' + bsW + 'px 0 ' + bsW + 'px rgba(0,0,0,.5)';
        }

        function resetFocus() {
            scEl.removeEventListener('click', resetFocus);
            setPVals(pEl, pRect, pBs);
            setPVals(eEl, eRect);
            document.querySelector('#ss-wrapper').classList.remove('hide');
            window.setTimeout(function() {
                pEl.style.display = 'none';
                pEl.className = '';
                eEl.style.display = 'none';
                ssEl.classList.remove('paused');
                focusActive = false;
            }, 1000);
            resetName();
        }

        function setFocus(planetName) {
            if(focusActive) {
                return;
            }
            focusActive = true;
            ssEl.classList.add('paused');
            pRect = overlayPlanet(planetName, pEl, true);
            eRect = overlayPlanet('earth', eEl);
            document.querySelector('#ss-wrapper').classList.add('hide');

            window.setTimeout(function () {
                eEl.style.top = '7%';
                eEl.style.left = '5%';
                eEl.style.width = '4em';
                eEl.style.height = '4em';
                calcFocusSize(pEl);
            }, 10);

            window.setTimeout(function () {
                scEl.addEventListener('click', resetFocus);
            }, 300);
        }

        function resetName() {
            let nEl = document.getElementById('ss-m-name'),
                listEl = document.getElementById('ss-info-ml');
            nEl.style.display = 'none';
            listEl.classList.remove('hide');
            document.getElementById('ss-m-info').classList.remove('active');
        }

        function moveName(mEl) {
            let nEl = document.getElementById('ss-m-name'),
                sEl = mEl.querySelector('span'),
                sStyles = window.getComputedStyle(sEl),
                iEl = document.getElementById('ss-info'),
                listEl = document.getElementById('ss-info-ml'),
                rect = sEl.getBoundingClientRect(),
                iRect = iEl.getBoundingClientRect();
            nEl.style.width = rect.width + 'px';
            nEl.style.height = rect.height + 'px';
            nEl.style.top = rect.top - iRect.top + 'px';
            nEl.style.marginLeft = sStyles.getPropertyValue('margin-left');
            nEl.style.paddingLeft = sStyles.getPropertyValue('padding-left');
            nEl.style.fontSize = sStyles.getPropertyValue('font-size');
            nEl.style.lineHeight = sStyles.getPropertyValue('line-height');
            nEl.style.display = 'block';

            nEl.innerText = sEl.innerText;

            listEl.classList.add('hide');
            return nEl;
        }

        function displayText(mission) {
            console.log(mission);
            if(missionData[mission]) {
                console.log(missionData[mission]);
                let md = missionData[mission];
                for (let i = 0; i < M__I_FIELDS.length; i++) {
                    document.querySelector('#ss-m-' + M__I_FIELDS[i] + ' span').innerHTML = md[M__I_FIELDS[i]];
                }
                document.getElementById('ss-m-info').classList.add('active');
            }
        }

        function displayInfo(mEl) {
            let nEl = moveName(mEl);
            window.setTimeout(function () {
                nEl.style.top = '15px';
                window.setTimeout(function() {
                    displayText(mEl.getAttribute('data-mission'));
                }, 500);
            }, 500);
        }

        document.querySelectorAll('[data-planet]').forEach(function(el) {
            el.addEventListener('click', function () {
                setFocus(this.getAttribute('data-planet'));
                displayInfo(this);
            });
        });

        function preload(imgAr) {
            let cacheEl = document.getElementById('ss-img-cache');
            for (let i = 0; i < imgAr.length; i++) {
                cacheImages[i] = new Image();
                cacheImages[i].src = imgAr[i];
                //cacheEl.appendChild(cacheImages[i]);
            }
        }
        preload(
            ['/assets/images/venuslarge.gif',
            '/assets/images/earthlarge.gif',
            '/assets/images/marslarge.gif',
            '/assets/images/jupiterlarge.gif',
            '/assets/images/saturnlarge.gif',
            '/assets/images/uranuslarge.png',
            '/assets/images/neptunelarge.gif']
        )

        setSSSize();

        window.setTimeout(function() {
            document.querySelector('#ss-wrapper').classList.remove('invisible', 'hide');
        }, 900);
    }

    function loadConfig() {
        function readJSON(path, callback) {
            var xhr = new XMLHttpRequest();
            xhr.open('GET', path, true);
            xhr.responseType = 'blob';
            xhr.onload = function (e) {
                if (this.status == 200) {
                    var file = new File([this.response], 'temp');
                    var fileReader = new FileReader();
                    fileReader.addEventListener('load', function () {
                        callback.call(window, JSON.parse(fileReader.result));
                    });
                    fileReader.readAsText(file);
                }
            }
            xhr.send();
        }

        readJSON('/assets/data/missions.json', function(data) {
            missionData = data;
        });
    }

    document.addEventListener('DOMContentLoaded', function (event) {
        loadConfig();
        init();
    });

})();